<template>
    <Header></Header>
    <main class="content container">
        <div class="content__top">
            <ul class="breadcrumbs">
                <li class="breadcrumbs__item">
                    <a class="breadcrumbs__link" href="index.html"> Каталог </a>
                </li>
                <li class="breadcrumbs__item">
                    <a class="breadcrumbs__link" href="cart.html"> Корзина </a>
                </li>
                <li class="breadcrumbs__item">
                    <a class="breadcrumbs__link"> Оформление заказа </a>
                </li>
            </ul>

            <h1 class="content__title">
                Заказ оформлен <span>№ {{ dataStatus.id }}</span>
            </h1>
        </div>

        <section class="cart">
            <form class="cart__form form" action="#" method="POST">
                <div class="cart__field">
                    <p class="cart__message">
                        Благодарим за&nbsp;выбор нашего магазина. На&nbsp;Вашу
                        почту придет письмо с&nbsp;деталями заказа. Наши
                        менеджеры свяжутся с&nbsp;Вами в&nbsp;течение часа для
                        уточнения деталей доставки.
                    </p>

                    <ul class="dictionary">
                        <li class="dictionary__item">
                            <span class="dictionary__key"> Получатель </span>
                            <span class="dictionary__value">
                                {{ dataStatus.name }}
                            </span>
                        </li>
                        <li class="dictionary__item">
                            <span class="dictionary__key">
                                Адрес доставки
                            </span>
                            <span class="dictionary__value">
                                {{ dataStatus.address }}
                            </span>
                        </li>
                        <li class="dictionary__item">
                            <span class="dictionary__key"> Телефон </span>
                            <span class="dictionary__value">
                                {{ dataStatus.phone }}
                            </span>
                        </li>
                        <li class="dictionary__item">
                            <span class="dictionary__key"> Email </span>
                            <span class="dictionary__value">
                                {{ dataStatus.email }}
                            </span>
                        </li>
                        <li class="dictionary__item">
                            <span class="dictionary__key"> Способ оплаты </span>
                            <span class="dictionary__value">
                                картой при получении
                            </span>
                        </li>
                    </ul>
                </div>

                <div class="cart__block">
                    <ul class="cart__orders">
                        <li
                            class="cart__order"
                            v-for="item in dataStatus.basket.items"
                            :key="item.id"
                        >
                            <h3>{{ item.product.title }}</h3>
                            <b
                                >{{
                                    $filters.numberFormat(item.product.price)
                                }}
                                ₽</b
                            >
                            <span>Артикул: {{ item.product.id }}</span>
                        </li>
                    </ul>

                    <div class="cart__total">
                        <p>Доставка: <b>500 ₽</b></p>
                        <p>
                            Итого:
                            <b>{{ dataStatus.basket.items.length }}</b> товара
                            на сумму
                            <b
                                >{{
                                    $filters.numberFormat(dataStatus.totalPrice)
                                }}
                                ₽</b
                            >
                        </p>
                    </div>
                </div>
            </form>
        </section>
    </main>
    -->
    <Footer></Footer>
</template>

<script>
import Header from "@/components/Header";
import Footer from "@/components/Footer";
import { mapState, mapGetters, mapActions, mapMutations } from "vuex";
import axios from "axios";
import { store_URL } from "@/axiosURL.js";

export default {
    components: {
        Header,
        Footer,
    },

    data() {
        return {};
    },

    computed: {
        ...mapState({
            dataStatus: (state) => state.basket.orderData,
            accessKey: (state) => state.basket.userAccessKey,
        }),
    },
    methods: {
        ...mapMutations({
            setOrederData: "basket/setOrederData",
        }),

        checkDataStatus() {
            const idPage = this.$route.params.id;
            if (this.dataStatus.id && this.dataStatus.id === idPage) {
                return false;
            }

            axios({
                method: "GET",
                url: store_URL + "orders/" + idPage,
                params: {
                    userAccessKey: this.accessKey,
                },
            })
                .catch((err) => {
                    consosle.log(err);
                })
                .then((response) => {
                    this.setOrederData(response.data);
                });
        },
    },

    created() {
        this.checkDataStatus();
    },
};
</script>

<style lang="scss" scoped>
</style>